    function getvalue(val){
		if(val == 0){
		return "VE/EBIDDA";
		}
		if(val == 90){
		return "VE/VENTAS";
		}
		if(val == 180){
		return "VE/ACTIVOS";
		}
		if(val == 270){
		return "VE/VALOR ACTIVOS";
		}
		//return val;
	}; 

